@import "styles/base/variables";
@import "styles/base/base";

$font-size-h1: 2.5rem !default;
$font-size-h2: 2rem !default;
$font-size-h3: 1.5rem !default;
$font-size-large: 1.1rem !default;
$font-size-small: 0.9rem !default;

.promo-landing-page {
  // Переносим фон из body
  background-color: $background-color-base;
  background-image: url("data:image/svg+xml;utf8,<svg width='40' height='40' viewBox='0 0 40 40' fill='none' xmlns='http://www.w3.org/2000/svg'><g opacity='0.04'><path d='M20 7a3 3 0 1 1 0 6 3 3 0 0 1 0-6zm0 7c2.21 0 4 1.79 4 4 0 1.3-.63 2.45-1.6 3.16C23.77 22.1 25 23.92 25 26h-10c0-2.08 1.23-3.9 2.6-4.84A3.98 3.98 0 0 1 16 18c0-2.21 1.79-4 4-4zm-5 21a1 1 0 0 1 0-2h10a1 1 0 1 1 0 2H15z' fill='%230088cc'/></g></svg>");
  background-repeat: repeat;
  background-size: 40px 40px;

  .promo-page {
    color: $text-color;
    font-family: $font-family-base;
    padding: 2rem 0;
  }
  .promo-section {
    &.promo-content-magnet-section {
      background-color: $background-color-light;
      padding: 4rem 0;
    }
  }
  .promo-container {
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
  }
  .promo-hero-content {
    text-align: center;
  }
  .promo-title {
    font-size: $font-size-h1;
    font-weight: $font-weight-bold;
    color: $text-color-headings;
    margin-bottom: 1.5rem;
    line-height: 1.3;
  }
  .promo-title-accent {
    color: $white-color;
    background-color: $primary-color;
    padding: 2px 8px;
    border-radius: $border-radius-sm;
    margin: 0 2px;
    white-space: nowrap;
  }
  .promo-subtitle {
    font-size: $font-size-large;
    line-height: $line-height-base * 1.1;
    color: $text-color-medium;
    margin-bottom: 1.5rem;
    + .promo-stats,
    + .promo-buttons {
      margin-top: 2rem;
    }
  }
  .promo-stats {
    margin-top: 10px;
    padding: 15px;
    border-radius: $border-radius-base;
    background-color: $background-color-accent;
    border-left: 4px solid $secondary-color;
    @media (max-width: ($breakpoint-lg - 1px)) {
      text-align: left;
      max-width: 450px;
      width: 100%;
    }
    .stats-title {
      font-size: 18px;
      font-weight: $font-weight-semibold;
      margin-bottom: 12px;
      color: $text-color;
    }
    .stats-list {
      list-style: none;
    }
    .stats-item {
      font-size: 15px;
      color: $text-color-medium;
      margin-bottom: 8px;
      display: flex;
      align-items: flex-start;
      &:last-child {
        margin-bottom: 0;
      }
      .stats-bullet {
        margin-right: 10px;
        color: $primary-color;
        line-height: inherit;
        flex-shrink: 0;
        margin-top: 2px;
      }
    }
    .stats-link {
      display: inline-flex;
      align-items: center;
      margin-top: 12px;
      font-size: 14px;
      font-weight: $font-weight-medium;
      color: $text-color-link;
      .stats-link-icon {
        margin-right: 6px;
        display: inline-flex;
        svg {
          width: 14px;
          height: 14px;
          stroke: currentColor;
        }
      }
      &:hover {
        color: $text-color-link-hover;
        text-decoration: underline;
      }
    }
  }
  .promo-buttons {
    margin-top: 2rem;
    margin-bottom: 2rem;
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
  }
  .promo-button {
    &--telegram {
      background-color: #0088cc;
      color: white;
      display: flex;
      align-items: center;
      gap: 8px;
      padding: 12px 24px;
      border-radius: 8px;
      transition: background-color 0.3s ease;
      &:hover {
        background-color: #006699;
      }
    }
  }
  .icon-telegram {
    display: inline-block;
    width: 20px;
    height: 20px;
    background-image: url("assets/telegram_logo.svg");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
  }
  @media (max-width: $breakpoint-sm) {
    .promo-page {
      padding: 1rem 0;
    }
    .promo-section {
      &.promo-content-magnet-section {
        padding: 2.5rem 0;
      }
    }
    .promo-container {
      padding: 0 10px;
    }
    .promo-title {
      font-size: $font-size-h1 * 0.75;
    }
    .promo-subtitle {
      font-size: $font-size-base;
    }
    .promo-buttons {
      flex-direction: column;
    }
    .promo-content-magnet {
      h2.content-magnet-title {
        font-size: $font-size-h2 * 0.8;
      }
      h3 {
        font-size: $font-size-h3 * 0.85;
      }
      p {
        font-size: $font-size-small;
      }
    }
  }
}
